@import "../../scss/variables";
@import "../../scss/libs/normalize";

.pizza {
  margin: 0 auto;
  width: 100%;
  max-width: 480px;
  text-align: center;
}

.title {
  margin-bottom: 20px;
  font-weight: 900;
  font-size: 20px;
  letter-spacing: 1%;
}

.image {
  max-width: 90%;
  object-fit: contain;
}

.selector {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding: 6px;
  background-color: #f3f3f3;

  ul {
    display: flex;
    flex: 1;

    &:first-of-type {
      margin-bottom: 6px;
    }

    li {
      @include noselect;

      flex: 1;
      padding: 8px;
      font-weight: 600;
      font-size: 14px;
      cursor: pointer;
    }
  }
}

.selector .active {
  border-radius: 5px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 4%);
  background: #fff;
  cursor: auto;
}

.bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
}

.price {
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  letter-spacing: 0.015em;
}